// ============================================
// GoHighLevel OAuth Callback API Route
// ============================================

import { NextRequest, NextResponse } from 'next/server'
import { cookies } from 'next/headers'

const GHL_AUTH_BASE = 'https://marketplace.gohighlevel.com/oauth'

const GHL_CONFIG = {
  clientId: process.env.GHL_CLIENT_ID || '',
  clientSecret: process.env.GHL_CLIENT_SECRET || '',
  redirectUri: process.env.GHL_REDIRECT_URI || `${process.env.NEXT_PUBLIC_APP_URL || 'http://localhost:3000'}/api/v1/import/ghl/callback`,
}

interface GHLTokenResponse {
  access_token: string
  token_type: string
  expires_in: number
  refresh_token: string
  scope: string
  userType: string
  locationId?: string
  companyId?: string
}

/**
 * GET /api/v1/import/ghl/callback
 * Handles the OAuth callback from GoHighLevel
 * Exchanges the authorization code for access tokens
 */
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const code = searchParams.get('code')
    const state = searchParams.get('state')
    const error = searchParams.get('error')
    const errorDescription = searchParams.get('error_description')

    // Handle OAuth errors
    if (error) {
      console.error('GHL OAuth Error:', error, errorDescription)
      return NextResponse.redirect(
        new URL(`/sub-account/settings/import?error=${encodeURIComponent(errorDescription || error)}`, request.url)
      )
    }

    // Verify code is present
    if (!code) {
      return NextResponse.redirect(
        new URL('/sub-account/settings/import?error=No authorization code received', request.url)
      )
    }

    // Verify state to prevent CSRF
    const cookieStore = await cookies()
    const storedState = cookieStore.get('ghl_oauth_state')?.value
    const subAccountId = cookieStore.get('ghl_oauth_subaccount')?.value

    if (!storedState || storedState !== state) {
      console.error('State mismatch:', { received: state, stored: storedState })
      return NextResponse.redirect(
        new URL('/sub-account/settings/import?error=Invalid state parameter', request.url)
      )
    }

    // Exchange code for tokens
    const tokenResponse = await fetch(`${GHL_AUTH_BASE}/token`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: new URLSearchParams({
        client_id: GHL_CONFIG.clientId,
        client_secret: GHL_CONFIG.clientSecret,
        grant_type: 'authorization_code',
        code,
        redirect_uri: GHL_CONFIG.redirectUri,
      }),
    })

    if (!tokenResponse.ok) {
      const errorData = await tokenResponse.json().catch(() => ({}))
      console.error('Token exchange failed:', tokenResponse.status, errorData)
      return NextResponse.redirect(
        new URL(`/sub-account/settings/import?error=Failed to exchange token: ${errorData.message || tokenResponse.statusText}`, request.url)
      )
    }

    const tokens: GHLTokenResponse = await tokenResponse.json()

    // Store tokens securely (in production, store in database)
    // For now, we'll store in encrypted cookies
    const tokenData = {
      accessToken: tokens.access_token,
      refreshToken: tokens.refresh_token,
      expiresAt: new Date(Date.now() + tokens.expires_in * 1000).toISOString(),
      locationId: tokens.locationId,
      companyId: tokens.companyId,
      scope: tokens.scope,
    }

    // Store connection data in secure cookie
    cookieStore.set('ghl_connection', JSON.stringify(tokenData), {
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      sameSite: 'lax',
      maxAge: tokens.expires_in, // Match token expiry
    })

    // Clear OAuth state cookies
    cookieStore.delete('ghl_oauth_state')
    cookieStore.delete('ghl_oauth_subaccount')

    // Redirect to import page with success
    return NextResponse.redirect(
      new URL('/sub-account/settings/import?connected=true&source=gohighlevel', request.url)
    )
  } catch (error) {
    console.error('GHL Callback Error:', error)
    return NextResponse.redirect(
      new URL(`/sub-account/settings/import?error=${encodeURIComponent(error instanceof Error ? error.message : 'Callback failed')}`, request.url)
    )
  }
}
